body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
    background-color: lightgray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.all-result-content{
    display: inline-flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: baseline;
}

.all-result-content > div.each-content {
    background-color: #f1f1f1;
    width: 28%;
    height: 10%;
    margin: 2%;
    font-size: 30px;
}

@media only screen and (max-width: 1200px) {
    .all-result-content > div.each-content {
        width: 40%;
        margin: 5%;
    }
    .no-content-to-show{
        margin-top: 100px;
    }
}

@media only screen and (max-width: 992px) {
    .all-result-content > div.each-content {
        width: 20%;
        margin: 2%;
    }
    .all-result-conten{
        flex-direction: column;
    }
    .no-content-to-show{
        margin-top: 50px;
    }
}
@media only screen and (max-width: 768px) {
    .all-result-content > div.each-content {
        width: 25%;
        margin: 2%;
    }

    .no-content-to-show{
        margin-top: 0;
    }
}

@media only screen and (max-width: 576px) {
    .all-result-content > div.each-content {
        width: 40%;
        margin: 5%;
    }
}

.each-content{
    position: relative;
    cursor:pointer;
}
.content-img{
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border-radius: 10px;
}
.middle-div{
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
}

.each-content:hover{
    z-index: 999;
}

.each-content:hover .content-img{
    opacity: 0.5;
    z-index: 999;
}
.each-content:hover .middle-div {
    opacity: 1;
}

.content-basic-info{
    width: 100%;
}
.content-basic-info .content-text{
    width: 100%;
    margin: 0;
    padding: 0;
    font-size: 17px;
    color: black;
    font-weight: 800;
}
.loading-bucket{
    background-color: black;
    height: 800px;
    position: absolute;
    width: 30%;
    z-index: 999;
    opacity: 0.8;
    border-radius: 10px;
}

@media only screen and (min-width: 993px) {
    .loading-bucket{
        width: 40%;
    }
}

@media only screen and (max-width: 992px) {
    .loading-bucket{
        width: 90%;
        height: 320px;
    }
    .loading-bucket img{
        margin-top: 100px !important;
    }
}

.loading-bucket img{
    margin-top: 250px;
    opacity: 1;
    width: 20%
}
.search-header-bucket{
    height: 100px;
    text-align: center;
}

.content-input{
    height: 30px;
    width: 300px;
    border-radius: 5px;
    font-size: 20px;
}

.type-search-div{
    margin-top: 5px;
}
.type-search-div label{
    margin: 0 5px;
    font-size: 26px;
}

.search-btn{
    margin-left: 15px;
}
.all-detailed-bucket{
    text-align: left !important;
    display: inline-flex;
    justify-content: center;
    flex-wrap: wrap;
}

.detailed-image{
    text-align: center;
    flex-shrink: 0;
}

.all-detailed-bucket > div.detailed-child{
    width: 45%;
    height: 10%;
    margin: 2%;
    font-size: 30px;
}

@media only screen and (max-width: 992px) {
    .bucket-child{
        display: none;
    }
    .responsive-div{
        display: inline-flex !important;
        overflow: auto;
        white-space: nowrap;
    }

    .responsive-div > div{
        max-height: 280px;
        max-width: 500px;
        display: inline-block;
        margin: 15px;
    }
    .responsive-synopsis{
        overflow: hidden;
        white-space: pre-wrap;
        width: 400px;
    }
    .responsive-synopsis .title{
        word-wrap: break-word
    }
    .responsive-detail, .responsive-other-series, .responsive-openings, .responsive-synopsis{
        text-align: left;
    }
    .responsive-detail .ordinary-text, .responsive-other-series .ordinary-text, .responsive-openings .ordinary-text, .responsive-synopsis .ordinary-text{
        font-size: 14px;
        font-weight: 200;
    }
    .responsive-detail .title, .responsive-other-series .title, .responsive-openings .title, .responsive-synopsis .title{
        font-size: 10px;
        font-weight: 600;
    }
}

.responsive-div{
    display: none;
}

.synopsis-content{
    padding: 0 25px;
}

.other-content-div table{
    margin-bottom: 15px;
}

.detailed-child .ordinary-text, .other-content-div .ordinary-text{
    font-size: 22px;
    color: black;
    font-weight: 200;
}
.detailed-child .title, .other-content-div .title{
    font-size: 15px;
    color: black;
    font-weight: 600;
}

.other-content-div table th, .other-content-div table td{
    padding: 5px 25px;
    text-align: left;
}

.content-badge{
    background-color: black;
    color: white;
    border-radius: 10px;
    margin: 2px;
    padding: 5px;
    line-height: 5px;
    font-size: 12px;
    font-weight: 200;
}

.trailer-btn-div{
    text-align: center;
}

.trailer-btn{
    margin-bottom: 15px;
}
.all-content-bucket{
    display: flex;
    justify-content: center;
    /*flex-direction: column;*/
}

.all-content-bucket > div.child-bucket {
    background-color: #f1f1f1;
    width: 40%;
    margin: 10px;
    text-align: center;
    font-size: 30px;
    border-radius: 10px;
    height: 800px;
    overflow-y: scroll;
    moz-box-shadow:    3px 3px 5px 6px #787878;
    box-shadow:         3px 3px 5px 6px #787878;
}

@media only screen and (max-width: 992px) {
    .all-content-bucket > div.child-bucket {
        width: 90%;
        margin: 5%;
        height: 320px;
    }
    .all-content-bucket{
        flex-direction: column;
    }
}

.search-input-div{
    margin-top: 20px;
}

.anime-btn{
    padding: 10px 15px;
    background-color: #ffa500;
    border-radius: 15px;
    color: white;
    font-size: 18px;
    cursor: pointer;
}
button:focus {outline:0;}
